body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F9EFF7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursor-pointer {
  cursor: pointer;
}

.bg {
  background-color: #000;
  width: auto;
  height: 853px;
  background-image: url(../public/bg.jpg);
  background-size: cover;
}

.title_cover {
  position: relative;

}

.title {
  margin: 0 auto;
  /* width: 500px; */
  height: 300px;
  line-height: 270px;
  position: absolute;
  top: 60px;
  left: 100px;
  z-index: 1;
  font-size: 30px;
  /* padding: 10px 0px; */
  box-shadow: 0 10px 20px rgba(0,0,0,0.1), 0 6px 6px rgba(0,0,0,0.1);
  border: none;
}

.page-link {
  color: #E97384;
  margin-top: 15px;
}

.page-link:hover {
  color: #E97384;
}

.active>.page-link,
.active>.page-link:hover {
  background-color: #E97384;
  border-color: #E97384;
  color: #fff;
}

.bottom {
  width: 100%;
  height: 50px;
  /* background-color: #000; */
  margin-top: 30px;
  color: #555;
  font-size: 15px;
  text-align: center;
  line-height: 50px;
}